import { useDispatch, useSelector } from "react-redux";
import { isEmpty } from "lodash";
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import ModalHolder from './ModalHolder';
import { setShowModal } from "../../Redux/reducer";

const DescriptionModal = () => {
    const showModal = useSelector(({ reducer }) => reducer.showModal);
    const dispatch = useDispatch();
    
    return (
        <Modal
            open={!isEmpty(showModal)}
            onClose={() => dispatch(setShowModal({}))}
        >
            <Fade in={!isEmpty(showModal)}>
                <ModalHolder>
                    <div className="modal__header">
                        <img src={ `${ process.env.PUBLIC_URL }/img/${showModal.item}/${showModal.type.img}` } alt={ showModal.type.img }/>
                    </div>
                    <div className="modal__content">
                        <h2>{ showModal.type.text }</h2>
                        <p>{ showModal.type.desc }</p>
                    </div>
                </ModalHolder>
            </Fade>
        </Modal>
    );
};

export default DescriptionModal;