import styled from "styled-components";

const ModalHolder = styled.div`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 1200px;
    min-width: 200px;
    background: #fff;
    border: 1px solid transparent;
    border-radius: 10px;

    .modal {
        &__header {
            padding: 0.5rem;
            img {
                width: 100%;
                display: block;
                object-fit: cover;
                border-radius: 10px;
            }
        }
        &__content {
            padding: 2rem;
            background: #000;
            border-radius: 30px 30px 10px 10px;
            color: #fff;
            text-align: center;
        }
    }
`

export default ModalHolder;