import styled from "styled-components";

const NavigationHolder = styled.div`
    position: sticky;
    top: 0;
    background: #fff;
    border-radius: 5px;
    ul {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        list-style: none;
        margin: 0.5rem 0 0.3rem;
        padding: 0;
        text-align: center;
        li {
            display: inline-block;
            margin: 0.5rem;
        }
    }
`

export default NavigationHolder;