import { useDispatch } from "react-redux";
import { toArray } from "lodash";
import { Menu } from "../../Data/Data";
import NavigationHolder from "./NavigationHolder";
import { setSelectedItem } from "../../Redux/reducer";

const Navigation = () => {
    const dispatch = useDispatch();
    return (
        <NavigationHolder>
            <ul>
                { toArray(Menu).map(({type}, index) => 
                    <li key={ index }>
                        <p onClick={ () => dispatch(setSelectedItem(type)) }>{ type }</p>
                    </li>
                )}
            </ul>
        </NavigationHolder>
    );
};

export default Navigation