import styled from "styled-components";

const ContentHolder = styled.div`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 0.5rem;
    padding: 0.5rem;
    height: calc(100vh - 226px);
    overflow: auto;
    div {
        padding: 1rem;
        background: #fff;
        min-width: 150px;
        border-radius: 5px;
        text-align: center;
        box-shadow: 5px 10px 18px rgba(0,0,0,0.3);
    }
    img {
        height: 100px;
        width: 100px;
        border-radius: 50%;
        object-fit: cover;
        box-shadow: 5px 10px 18px rgba(0,0,0,0.7);
    }
`

export default ContentHolder;