import { useSelector, useDispatch } from "react-redux";
import { Menu } from "../../Data/Data";
import { setShowModal } from "../../Redux/reducer";
import ContentHolder from "./ContentHolder";

const Content = () => {
    const selectedItem = useSelector(({ reducer }) => reducer.selectedItem);
    const dispatch = useDispatch();

    return (
        <ContentHolder>
            {
                selectedItem && Menu[selectedItem].data.map((type, index) => 
                <div key={ index }>
                    <img src={ `${ process.env.PUBLIC_URL }/img/${Menu[selectedItem].type}/${type.img}` } alt={ type.img }/>
                    <h3>{ type.text }</h3>
                    <p>{ `$${type.prc}` }</p>
                    <span onClick={ () => dispatch(setShowModal({item: Menu[selectedItem].type, type})) }>Ver Detalles</span>
                </div>
            )}
        </ContentHolder>
    );
};

export default Content;